import React, { Component } from "react";
import './App.css';
import ItemCard from "./components/ItemCard";
import Summery from './components/Summery';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      items: [
        {
          id: 1,
          name: "Meet and Greet with Bill Murray",
          detail: "Lorem ipoum dolor sitement, constructor edip",
          price: 19.95,
          count: 0,
          thumbnail: "data:image/jpeg;base64,"
        },
        {
          id: 2,
          name: "Social Shower Curtain",
          detail: "Lorem ipoum dolor sitement, constructor edip",
          price: 40.00,
          count: 0,
          thumbnail: "data:image/jpeg;base64,"
        },
        {
          id: 3,
          name: "Self Stirring Mug",
          detail: "Lorem ipoum dolor sitement, constructor edip",
          price: 12.35,
          count: 0,
          thumbnail: "data:image/jpeg;base64,"
        }
      ]
    };
  }

  handleItemCountChange = (id, count) => {
    const items = this.state.items;
    items[id - 1].count = count;
    this.setState({ items });
  }

  render() {
    let subtotal = 0;
    this.state.items.forEach(item => subtotal += item.price * item.count);
    return (
      <div className="App">
        <header className="App-header">
          <h1>Shopping Cart</h1>
          <div>Free shipping from $100</div>
        </header>
        <div className="ItemList">
          {
            this.state.items.map(
              item => (
                <ItemCard key={item.id} item={item}
                  handleItemCountChange={this.handleItemCountChange} />
              )
            )
          }
        </div>
        <Summery subtotal={subtotal} />
      </div>
    );
  }
}

export default App;
